import { useTranslation } from "react-i18next"

// ** React Imports
import { Link } from "react-router-dom"

// ** Custom Components
import Avatar from "@components/avatar"

// ** Store & Actions
import { store } from "@store/store"
import { getUser, deleteUser } from "@src/views/apps/user/store"

// ** Icons Imports
import {
  Slack,
  User,
  Settings,
  Database,
  Edit2,
  MoreVertical,
  FileText,
  Trash2,
  Archive
} from "react-feather"

// ** Reactstrap Imports
import {
  Badge,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap"

import Moment from "moment"

// ** Renders Client Columns
const renderClient = (row) => {
  if (row.avatar) {
    return <Avatar className="me-1" img={row.avatar} width="32" height="32" />
  } else {
    return (
      <Avatar
        initials
        className="me-1"
        color={row.avatarColor || "light-primary"}
        content={row.name || "John Doe"}
      />
    )
  }
}

// ** Renders Role Columns
const renderRole = (row) => {
  const roleObj = {
    subscriber: {
      class: "text-primary",
      icon: User
    },
    maintainer: {
      class: "text-success",
      icon: Database
    },
    editor: {
      class: "text-info",
      icon: Edit2
    },
    author: {
      class: "text-warning",
      icon: Settings
    },
    admin: {
      class: "text-danger",
      icon: Slack
    }
  }

  const Icon = roleObj[row.role_name] ? roleObj[row.role_name].icon : Edit2

  return (
    <span className="text-truncate text-capitalize align-middle">
      <Icon
        size={18}
        className={`${
          roleObj[row.role_name] ? roleObj[row.role_name].class : ""
        } me-50`}
      />
      {row.role_name}
    </span>
  )
}

const renderName = (name) => {
  // ** Hooks
  // const { t } = useTranslation()

  // const { t } = useTranslation()
  // console.log(t(name))
  return name
}

const statusObj = {
  pending: "light-warning",
  active: "light-success",
  inactive: "light-secondary"
}

const Columns = () => {
  // ** Hooks
  const { t } = useTranslation()

  return [
    {
      name: renderName("User"),
      sortable: true,
      minWidth: "300px",
      sortField: "name",
      selector: (row) => row.name,
      cell: (row) => (
        <div className="d-flex justify-content-left align-items-center">
          {renderClient(row)}
          <div className="d-flex flex-column">
            <Link
              to={`/apps/user/view/${row.id}`}
              className="user_name text-truncate text-body"
              // onClick={() => store.dispatch(getUser(row.id))}
            >
              <span className="fw-bolder">{row.name}</span>
            </Link>
            <small className="text-truncate text-muted mb-0">{row.email}</small>
          </div>
        </div>
      )
    },
    {
      name: "Role",
      sortable: true,
      minWidth: "172px",
      sortField: "role_name",
      selector: (row) => row.role_name,
      cell: (row) => renderRole(row)
    },
    // {
    //   name: "Unit",
    //   minWidth: "138px",
    //   sortable: true,
    //   sortField: "unit",
    //   selector: (row) => row.currentPlan,
    //   cell: (row) => <span className="text-capitalize">{row.currentPlan}</span>
    // },
    {
      name: "Joined Date",
      minWidth: "230px",
      sortable: true,
      sortField: "joined_date",
      selector: (row) => row.billing,
      cell: (row) => (
        <span className="text-capitalize">
          {Moment(row.joined_date).format("DD MMM YYYY")}
        </span>
      )
    },
    {
      name: "Status",
      minWidth: "138px",
      sortable: true,
      sortField: "status",
      selector: (row) => row.is_active,
      cell: (row) => (
        <Badge
          className="text-capitalize"
          color={statusObj[row.is_active ? "active" : "inactive"]}
          pill
        >
          {row.is_active ? "active" : "inactive"}
        </Badge>
      )
    },
    {
      name: "Actions",
      minWidth: "100px",
      cell: (row) => (
        <div className="column-action">
          <UncontrolledDropdown>
            <DropdownToggle tag="span">
              <MoreVertical size={14} className="cursor-pointer" />
            </DropdownToggle>
            <DropdownMenu end>
              <DropdownItem
                tag={Link}
                className="w-100"
                to={`/apps/user/view/${row.id}`}
                onClick={() => store.dispatch(getUser(row.id))}
              >
                <FileText size={14} className="me-50" />
                <span className="align-middle">Details</span>
              </DropdownItem>
              <DropdownItem
                tag="a"
                href="/"
                className="w-100"
                onClick={(e) => e.preventDefault()}
              >
                <Archive size={14} className="me-50" />
                <span className="align-middle">Edit</span>
              </DropdownItem>
              <DropdownItem
                tag="a"
                href="/"
                className="w-100"
                onClick={(e) => {
                  e.preventDefault()
                  store.dispatch(deleteUser(row.id))
                }}
              >
                <Trash2 size={14} className="me-50" />
                <span className="align-middle">Delete</span>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
      )
    }
  ]
}
export default Columns
